import { Outlet, NavLink, useLocation } from "react-router-dom";
import styles from './account.module.css';
import { useDispatch } from "react-redux";
import { userLogout } from "../../services/actions/authentication";

export const Account = () => {
  const location = useLocation();
  const dispatch = useDispatch();
  const active = isActive =>
                  isActive
                        ? `text text_type_main-medium text_color_inactive ${styles.link} ${styles.active}`
                        : `text text_type_main-medium text_color_inactive ${styles.link}`;

  const onClick = () => {
    dispatch(userLogout());
  }
  
  return (
    <div className={styles.container}>
      <nav className={styles.nav}>
        <ul className={styles.list}>
          <li className={styles.item}>
            <NavLink to='/profile' end className={({isActive}) => active(isActive)}></NavLink>
          </li>
          <li className={styles.item}>
            <NavLink to='/profile/orders' className={({isActive}) => active(isActive)}> </NavLink>
          </li>
          <li className={styles.item}>
            <button onClick={onClick} className={`text text_type_main-medium text_color_inactive ${styles.button}`}></button>
          </li>
        </ul>
        {location.pathname === '/profile'
              ? <p className={`text text_type_main-default text_color_inactive ${styles.caption}`}>     <br />    </p>
              : <p className={`text text_type_main-default text_color_inactive ${styles.caption}`}>     <br />    </p>
        }
      </nav>
      <Outlet />
    </div>
  )
}